.nav{
    position: fixed;
    top:0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index:1;

    /*Animations*/

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_black{
    background-color: #111;
}

.nav_logo{
    position: fixed;
    left: 20px;
    width:80px; /*makes the logo smaller*/
    object-fit:contain; /*makes the picture fit in the div without compromising looks*/
}

.nav_avatar {
    position: fixed;
    right:20px;
    width: 30px;
    object-fit: contain;

}